import React, { useEffect, useState } from 'react';
import {NavLink} from "react-router-dom";
// import './SideNav.css';
import './../../assets/style.css';
import {AiOutlineArrowLeft, AiOutlineRobot, AiOutlineHistory, AiOutlineSetting, AiOutlineUser} from 'react-icons/ai'
import {FaEquals,FaMapMarkerAlt, FaParking} from 'react-icons/fa'

function SideNavPresenter({isAuthorized}) {
    const menus = [
        { name: "실시간 로봇위치", path: "/map", iconClassName: "bi bi-speedometer2"},
        { name: "로봇 호출", path: "/call", iconClassName: "FaRobot" },
        { name: "픽업대 상황", path: "/pickup", iconClassName: "bi bi-speedometer2" },
        { name: "기록", path: "/history", iconClassName: "bi bi-speedometer2" },
        { name: "설정", path: "/setting", iconClassName: "bi bi-speedometer2" }

    ];
    const [isFullNav, setIsFullNav] = useState(true);

    const dbLogoSrc = sessionStorage.getItem('logoSrc');
    const logo_src = '/assets/store_logo/' + dbLogoSrc + '.png';
    // const logo_src = '/assets/store_logo/the_people_burger/the_people_burger_logo.png'

    return (
        <div id="side-nav" className={"side-nav "+(isFullNav ? "big-nav" : "small-nav")}>
            <div id="title">
                <div onClick={() => setIsFullNav(!isFullNav)} className="toggle-menu-btn">
                    {isFullNav ? (
                        <AiOutlineArrowLeft />
                    ) : (
                        <FaEquals />
                    )}
                </div>
                <img src={"/assets/hprobot-logo.png"} alt="hprobot-logo" />
                {/* <img src={require("../../assets/simbol.png").default} alt="hprobot" /> */}
                <div>Helper Robotics</div>

            </div>
            {/* {menus.map((menu, idx) => {
                return (
                    <NavLink to={menu.path} key={idx} className="nav-item" activeClassName="nav-active">
                        {menu.name}
                    </NavLink>
                );
            })} */}
            <NavLink to="/map" className="nav-item" activeClassName="nav-active">
                <div className="nav-icon"><FaMapMarkerAlt /></div>
                <p>실시간 로봇위치</p>
            </NavLink>
            <NavLink to="/call" className="nav-item" activeClassName="nav-active">
                <div className="nav-icon"><AiOutlineRobot /></div>
                <p>로봇 호출</p>
            </NavLink>
            <NavLink to="/pickup" className="nav-item" activeClassName="nav-active">
                <div className="nav-icon"><FaParking /></div>
                <p>픽업대 상황</p>
            </NavLink>
            <NavLink to="/history" className="nav-item" activeClassName="nav-active">
                <div className="nav-icon"><AiOutlineHistory /></div>
                <p>기록</p>
            </NavLink>
            <NavLink to="/setting" className="nav-item" activeClassName="nav-active">
                <div className="nav-icon"><AiOutlineSetting /></div>
                <p>설정</p>
            </NavLink>

            {isAuthorized ?

                <NavLink to='/my-page' id="nav-user">
                        {/* <img src={require('/assets/store_logo/the_people_burger/the_people_burger_logo.png').default} alt="logo" /> */}
                        <img src={logo_src} alt="user-logo" />

                {/* userid로 user img url 불러오기 */}
                    <p>{sessionStorage.getItem('userName')} 매장</p>
                </NavLink>
                :

            <NavLink to='/login' id="nav-login">
                <div className="nav-icon"><AiOutlineUser /></div>

            <p>로그인</p>
        </NavLink>
        
        }
            

        </div>
    );
}

export default SideNavPresenter;
