export async function getCallRobot(userId) {    // 호출 버튼을 눌러 쉬고있는 로봇 하나를 user의 위치로 오도록 명령을 내린다.


    // [안전장치1] 조금의 버퍼링으로 로봇을 2번 호출하면 안됨.
    try {
        axios.get('/controller/button/robot-updated/' + userId)  // 최근 로봇 수정이 1초안에 있었다면 기달
            .then((response) => {
                console.log("[CallController] getCallRobot - robot-updated  결과 ->");
                console.log(response);
                if (response.data.result.length) {  // 1초내 변경된 로봇의 결과가 1개 이상이라면
                    var message = "[CallController] getCallRobot - update in 3 second. please wait";
                    return [true, message];
                }
            })
            .catch((error) => {
                console.log("[CallController] getCallRobot - robot-updated  fail get robot updated.");
                console.log(error);
                return [error, response.data.message];
            })
        // [안전장치2] 조금의 버퍼링으로 로봇을 2번 호출하면 안됨.

        axios.get('/controller/button/is-call/' + userId)  // 어떤 로봇이 오고있다면 반환
            .then((response) => {
                console.log("[CallController] getCallRobot - is call 결과 ->");
                console.log(response);
                if (response.status == 201) {
                    var message = "[CallController] getCallRobot - some robot is coming";
                    return [true, message];
                }
            })
            .catch((error) => {
                console.log("[CallController] getCallRobot - fail  get is call status.");
                console.log(error);
                return [error, response.data.message];
            })



        const response = await axios.get('/controller/button/to-user/' + userId);
        let error = response.data.error;
        console.log("[CallController] getCallRobot - to-user  결과 ->");
        console.log(response);
        if (error) { //error일때, 즉 response.state가 201 또는 400일때
            console.log(response.data.message);
            return [error, response.data.message]
        }
        else {  // error 안났을때, 200
            let robotId = response.data.robot_id;
            let userNode = response.data.user_node;
            let mapName = response.data.map_name;
            let postError = postButton([userNode, mapName, robotId]);
            return [postError, robotId];
        }
    } catch (e) {
        console.log("[CallController] getCallRobot 호출 가능한 로봇이 없습니다. 기다려주세요.\n" + e.message);
        return [true, e.message]
    }
}
function postButton(params) {
    console.log("[CallController] postButton param ", params);
    try {
        const response = axios({
            method: 'post',
            url: '/mqtt/button',
            data: {
                final_node: params[0],
                map_name: params[1],
                robot_id: params[2]
            }
        });
        console.log("[CallController] postButton   결과 ->");
        console.log(response);
        return false;

    } catch (e) {
        console.log("[CallController] postButton button 정보를 로봇으로 보내는데 실패했습니다.\n" + e.message);
        return [true, e.message]
    }

}