import React from 'react'

function PickupContent({ pickup }) {
    const isNull = pickup.phone === "";
    return (
        <li className="pickup-item">
            <div className="pickup-number"> {pickup.id}</div>
            <div className="pickup-store"> {pickup.store}</div>
            <div className="pickup-status">
                {isNull ?
                    <div> 비어있음</div>
                    :
                    <div>
                        <div> 배달 주소 : {pickup.delivery}</div>
                        <div> 연락처 : {pickup.phone}</div>
                        <div> 대기시간 : {pickup.delay}</div>
                    </div>
                }

            </div>
        </li>
    )
}

function PickupItem({pickupList}) {
    return (
        <div>
            <ul>
                {pickupList.map(pickupItem => (
                    <PickupContent pickup={pickupItem} key={pickupList.id}/>
                ))}
            </ul>
        </div>
    )
}

export default PickupItem
