import React, {useState, useEffect} from 'react'

import axios from 'axios';
axios.defaults.baseURL = process.env.REACT_APP_SERVER_URL;    //15.165.182.10

// axios.defaults.baseURL = 'http://localhost:8080/server/src/public';    //15.165.182.10
// axios.defaults.baseURL = 'http://15.165.182.103:8080/server/src/public';     //15.165.182.103
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

function RobotItem(props) {
    const statusColor = ["gray", "green", "red", "orange", "yellow", "blue"]
    const colorString= statusColor[props.state];
    const posX = (props.posX-(-0.3)) * props.unit; //-0.5
    const posY = (props.posY-(-0.3)) * props.unit;
    const robotId = props.robotId;
    const robotSize = (props.unit*0.5)+'px';
    const robotImgSrc = "/assets/robot/num_robot_" + robotId + ".png";
    return (
        <div className="robot" style={{ left: posX, top: posY, backgroundColor: colorString, height: robotSize, width: robotSize}}>
            <img src={robotImgSrc} alt="num-robot-img" className='num-robot-img'/>
        </div>
    )

}
function calcAngleDegrees(x, y) {   // 하나의 좌표와 원점사이의 arctan(degree각도) 계산
    return Math.atan2(y, x) * 180 / Math.PI;
}

function TargetItem(props){

    const ax = (props.currX-(-0.5)) * props.unit;
    const ay = (props.currY-(-0.5)) * props.unit;
    const bx = (props.finalX-(-0.5)) * props.unit;
    const by = (props.finalY-(-0.5)) * props.unit;
    // 선분의 중심은 두 점사이의 중심
    const centerX = (ax+bx)/2;
    const centerY = (ay+by)/2;

  
    // 선분의 길이는 두 점 사이의 길이 (width)
    const size = Math.sqrt(Math.pow(ax-bx, 2) + Math.pow(ay-by,2));


    // target -형태의 div 시작위치는 중점에서 길이의 반절만큼 왼쪽으로 가야함
    const top = centerY;
    const left = centerX - (size)/2;

   
    // 수평선으로부터 회전된 각도는 한 점이 원점이라고 할 때 arctan 값
    const degree = calcAngleDegrees(ax-bx, ay-by);
    const transformString = "rotate("+degree.toString() + "deg)"
    return (
        <div className="target" style={{ left: left, top: top, width: size, transform:transformString}}>
        </div>
    )
    
}
function RobotPosition({unit, setRobotStatus}) {


    // const unit = props.unit;
    const [robotPositionList, setRobotPositionList] = useState([]);
    // 덤프데이터..
    const dumpRobotPosition =  [
        {
            map_id: 7, robot_id: 1, state: 2, current_node: 1, final_node: 18, battery:100,
            curr_x: 9, curr_y: 2, final_x: 4, final_y: 2
        },
        {
            map_id: 7, robot_id: 2, state: 2, current_node: 2, final_node: 2, battery:100,
            curr_x: 10, curr_y: 2, final_x: 10, final_y: 2
        },
        {
            map_id: 7, robot_id: 3, state: 2, current_node: 3, final_node: 3, battery:100,
            curr_x: 11, curr_y: 2, final_x: 11, final_y: 2
        }
    ]

    const mapId = sessionStorage.getItem('mapId');

    function getRobotPosition() {
        var interval = setInterval(() => {
            console.log("[RobotPosition] get robot position")
            axios.get('/controller/robot/position/' + mapId)
                .then((response) => {
                    setRobotPositionList(response.data.robot_position);
                    setRobotStatus(response.data.robot_position);
                })
                .catch((error) => {
                    console.log("call robot positions item fail.");
                    setRobotPositionList(dumpRobotPosition);
                    clearInterval(interval);
                    return interval;

                })
        }, 1000);
        return interval
    }


    useEffect(() => {
        const interval = getRobotPosition();
        return () => clearInterval(interval);
    }, [])
    /* [robotPositionList] 쓰는 이유. (근데 쓰면 계속 불러오드람. 더 찾아보기.)
    근데 여기서 새로고침을 하지 않으면 등록된 모습을 확인할 수 없다. 그 이유는 useEffect 함수의 두번째 인자로 빈 배열을 주면 처음 사이트에 들어갔을 1회만 렌더링이 되기 때문에 다시 값을 불러오지 않는다.
    그래서 다음과 같이 수정해 주면 등록이 완료될 때 게시판이 업데이트 되는 모습을 확인할 수 있다.*/

    //불러온 데이터가 존재 할 경우 curr_x: 11, curr_y: 2, final_x: 11, final_y: 2
    if (robotPositionList.length > 0) {
        return (
            <div>
                {robotPositionList.map(robot => (
                    <RobotItem 
                    unit={unit} robotId={robot.robot_id} state={robot.state} posX={robot.curr_x} posY={robot.curr_y}
                    key={robot.robot_id} />
                ))}
                {robotPositionList.filter(robot => robot.state == 1).map(frobot => (
                    <TargetItem 
                    unit={unit} currX={frobot.curr_x} currY={frobot.curr_y} finalX={frobot.final_x} finalY={frobot.final_y}
                    key={frobot.robot_id} />
                ))}

            </div>
        )
    }else {  // 조회 데이터 존재하지 않을 경우
        console.log(" robotPositionList.length <= 0 존재안함")
        return (
            <div>
                <button className="data-reload" onClick={getRobotPosition}>데이터 로드</button>
            </div>
        )
    }


    // return (
    //     <div className="robot" id={idString} style={{ left: posX, top: posY}}>
    //         {/* {idString} */}
    //     </div>
    // )
}


export default RobotPosition
