import React from 'react'

function MapTile(props) {
    const unit = props.unit;
    const nodeId = props.data['node_id'];
    const posX = (props.data['x']) * unit;
    const posY = (props.data['y']) * unit;
    const type = props.data['type'];
    const mapUnitImgSrc = "/assets/unit/map_unit_" + type + ".png";
    return (
        <div className="map-tile" onClick={props.onClick} style={{ width: unit, height:unit, left:posX, top:posY}}>
            <div className="map-tile-node-id">{nodeId}</div>
            <img src={mapUnitImgSrc} alt="" className='map-tile-img' style={{ width: unit, height:unit}} />

        </div>
    )

}

function MapRail(props) {
    
    const unit = props.unit;
    const xytypeList = props.xytypeList;
    const mapId = sessionStorage.getItem('mapId');
    const mapBackgroundSrc = "assets/map-background/map-background-"+mapId+".png";
    return (
        <div>
            {xytypeList.map(xytype => (
                <MapTile unit={unit} data={xytype} key={xytype['node_id']} onClick={() => props.onClick(xytype['node_id'])}/>
            ))}
            <img src={mapBackgroundSrc} alt="map-background-img" className="map-background-img" />

        </div>
    )
}

export default MapRail
