import React, {useState} from 'react'
import './../../assets/style.css';
import {postControllerLogin} from './LoginController';

function Login() {
    // 테스트용 user 정보
    const dumpUsers = [
        { id: "hello", password: "world", mapId:7, userName:"store21"},
        { id: "admin", password: "1234", mapId:7, userName:"store14"},
        { id: "a", password: "s", mapId:7, userName:"store17"}
    ];
    const checkLogin = (id, pw) => {
        const user = dumpUsers.find(
            (user) => user.id === id && user.password === pw
        );
        if (user === undefined) {
            // alert('user undefined.'+id)
            return null;}
        return user;
    }
    // 아이디, 패스워드 입력값 변수. 초기는 null
    const [inputId, setInputId] = useState("");
    const [inputPassword, setInputPassword] = useState("");

    // input에 입력시 변수 갱신
    const handleInputId = (e) => {
        setInputId(e.target.value)
    }

    const handleInputPassword = (e) => {
        setInputPassword(e.target.value)
    }

    const onSubmit = async () => {
        // 서버로 로그인 정보 확인
        let [error, result] = await postControllerLogin(inputId, inputPassword);
        if (!error) {
            // console.log("[LoginPresenter] post login success");
            if (result.length >= 1) {
                console.log("[LoginPresenter] login success : ");
                console.log(result[0].user_name);
                // const userName = 
                sessionStorage.setItem('userId', inputId)
                sessionStorage.setItem('mapId', result[0].map_id)
                sessionStorage.setItem('userName', result[0].user_name)
                sessionStorage.setItem('logoSrc', result[0].logo_src)
                document.location.href = '/'

            }
            else {
                console.log("[LoginPresenter] login failed.");
                return;
            }
        }
        else {
            console.log("[LoginPresenter] post login failed, error  : ", result);
            return;

        }


/*
        const user = checkLogin(inputId, inputPassword);
        if (user === null){
            console.log('입력하신 정보가 올바르지 않습니다.')
        } else {
            
            sessionStorage.setItem('userId', inputId)
            sessionStorage.setItem('mapId', user.mapId)
            sessionStorage.setItem('userName', user.userName)
            

        }
        작업 완료 되면 페이지 이동(새로고침)
        document.location.href = '/'
        */
    }
    
    return (
        <div className="content" id="login">
            <h1 > 로그인</h1>
            <div className="grid-container">
                <div className="grid-item" id="item-form">
                        <label htmlFor='input_id'>아이디</label>
                        <br/>
                        {/* 한줄씩 하려고 br 태그 사용함. 나중에 부트스트랩 사용으로 간편하게 고칠예정 develop */}
                        <input type='text' name='input_id' value={inputId} onChange={handleInputId}  />
                        {/* <div><input type='id' id="id"/></div> value를 지정안해서 입력 그대로 보임. 갱신상황과 상관없음 */}
                        <br/> <br/>
                        <label htmlFor='input_pw'>비밀번호</label> 
                        <br/>
                        <input type='password' name='input_pw' value={inputPassword} onChange={handleInputPassword} />
                        <br/> <br/>
                        <button onClick={onSubmit}>로그인</button>
                </div>
            </div>
        </div>
    )
}

export default Login
