import React, { Component } from 'react';
import Modal from './../Modal/Modal';
const BUTTON_WRAPPER_STYLES = {
    position: 'relative',
    zIndex: 1
  }
  

export class SettingPresenter extends Component {
    constructor(props){
        super(props)

        this.state = {
            isModalOpen: false
        };
    }
    onLogout = () => {
        window.sessionStorage.removeItem('userId');
        window.sessionStorage.removeItem('mapId');
        window.sessionStorage.removeItem('userName');
        window.sessionStorage.removeItem('logoSrc');

        document.location.href = '/'
        
    }
    render() {
        const isModalOpen = this.state.isModalOpen;
        console.log("[Setting presenter] username");
        console.log(sessionStorage.getItem('userName'));
        return (
            <div className="content setting">
                <h1>설정</h1>
                <div className="grid-container">
                    <div className="grid-item">
                    설정 페이지입니다
                    <button onClick={this.onLogout.bind(this)}>로그아웃</button>
                        <div style={BUTTON_WRAPPER_STYLES} onClick={() => console.log('clicked')}>
                            <button onClick={() => this.setState(prevState => ({isModalOpen:true }))}>Open Modal</button>

                            <Modal title="제목" content = "내용22" open={isModalOpen} onCancel={() => this.setState(prevState => ({isModalOpen:false }))}
                            onPermit={() => this.setState(prevState => ({isModalOpen:false }))}>
                            </Modal>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

export default SettingPresenter
