import axios from 'axios';
axios.defaults.baseURL = process.env.REACT_APP_SERVER_URL;    //15.165.182.103
// axios.defaults.baseURL = 'http://localhost:8080/server/src/public';    //15.165.182.10
// axios.defaults.baseURL = 'http://15.165.182.103:8080/server/src/public';    //15.165.182.10
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

export async function getMapView() {    // map화면에서 목적지 node를 눌렀을 때, 픽업대의(1번) 로봇을 보내는 함수
    
    const mapId = sessionStorage.getItem('mapId');
    try {
        const response = await axios.get('/map-views/'+mapId);  // 해당 map id의 map view얻기
        
        if (response.status == 200) { 
            return [false, response.data]
        }
        else {
        // console.log("[MapController] getMapView server error.\n");
        return [response.data.error, response.data.message];
        }
    } catch (e) {
        console.log("[MapController] getMapView error catch.\n" + e.message);
        
        return [true, e.message]
    }
}
