import React, { Component } from 'react'

export class User extends Component {
    onLogout = () => {
        window.sessionStorage.removeItem('userId');
        window.sessionStorage.removeItem('mapId');
        window.sessionStorage.removeItem('userName');
        window.sessionStorage.removeItem('logoSrc');

        document.location.href = '/'

    }
    render() {
        const userName = sessionStorage.getItem('userName')
        const dbLogoSrc = sessionStorage.getItem('logoSrc');
        const logo_src = '/assets/store_logo/' + dbLogoSrc + '.png';
        // const logo_src = '../../assets/store_logo/the_people_burger/the_people_burger_logo.png'
        return (
            <div className="content" id="user">
                <h1 > 내 정보</h1>
                <div className="grid-container">
                    <div className="grid-item" id="item-profile">
                        <h4>프로필</h4>
                {/* userid로 user 정보와 img url 불러오기 */}
                        <img src={logo_src} alt="user-logo" />

                        <div> 매장명 : {userName}</div>
                        <button onClick={this.onLogout.bind(this)}>로그아웃</button>
                    </div>
                    <div className="grid-item" id="item-2">
                        <h4>호출 통계</h4>
                    </div>
                    <div className="grid-item" id="item-3">
                        <h4>최근 주문</h4>
                    </div>
                    <div className="grid-item" id="item-4">
                        <h4>뭐할까</h4>
                    </div>
                </div>
            </div>
        )
    }
}

export default User
