import React, { Component } from 'react';
import CallStatus from './CallStatus';
import {getBackRobot, getCallRobot, getIsCall} from './CallController';
import CallButton from './CallButton';
export class CallPresenter extends Component {
    constructor(props) {
        super(props)

        this.state = {
            isCall: 0,
            calledRobot: 0,
            count: 2,
            loading: 1
        };

    }
    updateIsCall(input) {
        this.setState(prevState => ({
            ...prevState,
            isCall: input
        }))

    }
    updateLoading(input) {
        this.setState(prevState => ({
            ...prevState,
            loading: input
        }))

    }
    updateCalledRobot(input) {
        this.setState(prevState => ({
            ...prevState,
            calledRobot: input
        }))

    }
    async toggle(userId) {
        const isCall = this.state.isCall;
        const count = this.state.count;
        console.log("[CallPresenter] 버튼 클릭. 버튼상태: " + isCall)
        if (this.state.loading) {
            console.log("[CallPresenter] loading...");
            return;
        } else {
            this.updateLoading(1);  //연속클릭 못하고 처리 후 클릭하도록 잠금
        }

        switch (isCall) {
            case 2:     // 매장에 로봇이 도착한 상태. 픽업대 버튼을 누를때 시행되는 상황
                let [error, message] = await getBackRobot(userId);
                if (!error) {
                    console.log("[CallPresenter] toggle back, robot_id : ", message);
                    this.setState(prevState => ({
                        ...prevState,
                        isCall: 0,
                        count: count + 1     //실제 로봇수 아님, 임시 테스트용
                    }))
                }
                else {
                    console.log("[CallPresenter] toggle back, error  : ", message);

                    // alert(message);
                }
                break;
            case 0:     // 로봇이 home에 있는 상태. 호출 버튼을 누를때 시행되는 상황
                let [error2, message2] = await getCallRobot(userId);
                if (!error2) {
                    console.log("[CallPresenter] toggle call, robot_id : ", error2);
                    this.setState(prevState => ({
                        ...prevState,
                        isCall: 1,
                        count: count - 1,
                    }))
                }
                else {
                    console.log("[CallPresenter] toggle call, error  : ", message2);
                    // alert(message2);
                }
                break;
            default:
                break;
        }
        this.updateLoading(0);  // 처리 후 잠금 푼다.

    }

    render() {
        const userId = sessionStorage.getItem('userId');
        const isCall = this.state.isCall;

        return (
            <div className="content" id="call">
                <h1 > 로봇 호출</h1>
                <div className="grid-container">
                    <div className="grid-item" id="item-button">
                        <h4>로봇 호출 버튼</h4>
                        <div id="calling-map-name">
                            {userId}의 매장 입니다.
                        </div>
                        <div id="called-robot">
                            {isCall != 0 ?
                                <div><span>{this.state.calledRobot}</span> 번 로봇 배정 </div>  //state에서 직접 불러옴. render위에서 부르면 왜인지 object뜸
                                :
                                <div> </div>
                            }

                        </div>
                        {/* <button id="call-button" onClick={this.toggle.bind(this, userId, mapId, userName)}>
                {isCall? "픽업대" : "호출"} updateCalledRobot
            </button> */}
                        <CallButton userId={userId} isCall={isCall} onClick={() => this.toggle(userId)}
                            updateIsCall={(input) => this.updateIsCall(input)} updateCalledRobot={(input) => this.updateCalledRobot(input)} updateLoading={(input) => this.updateLoading(input)} />
                    </div>
                    <div className="item-call-status">
                        <CallStatus />

                    </div>

                </div>
            </div>
        )
    }
}

export default CallPresenter
