import React, {useState, useEffect} from 'react'
// import {GetRobotStatus} from './CallController';
import './../../assets/style.css';

import axios from 'axios';
axios.defaults.baseURL = process.env.REACT_APP_SERVER_URL;    //15.165.182.10

// axios.defaults.baseURL = 'http://localhost:8080/server/src/public';    //15.165.182.10
// axios.defaults.baseURL = 'http://15.165.182.103:8080/server/src/public';    //15.165.182.103
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

// -- 로봇 status 하나의 항목
function RobotStatus({robot}){
    return (
        <li>
            <span> {robot.robot_id}</span>
            <span> - {robot.used == '0' ? "대기" : robot.used}</span> 
            <span> [ {robot.battery}% ]</span>
        </li>
    )
}

// -- 로봇 status 리스트에 필요한 작업(list)
function CallStatus() {
    const [robotStatusList, setRobotStatusList] = useState([]);

    const mapId = sessionStorage.getItem('mapId');

    function getRobotStatus(){
        axios.get('/controller/robot/status/'+mapId)
        .then((response)=>{
            setRobotStatusList(response.data.robot_status);            
        })
        .catch((error) => {
            console.log("call robot status item fail.");
        })
    }
    function countNotUsed(){
        // console.log(" lenghtheth : ");
        const count = robotStatusList.filter(robot => robot.used === "0");  //아무도 사용중이지 않은 로봇 ('0')의 수 계산
        return count.length;
        // if (robotStatusList.length > 0) {
        //     const count = robotStatusList.filter(robot => robot.used === "0");
        //     return count.length;
        // }s
        // else {
        //     return 0;
        // }
    }

    useEffect(() => {
        getRobotStatus();
    }, [robotStatusList])
    /* [robotStatusList] 쓰는 이유. (근데 쓰면 계속 불러오드람. 더 찾아보기.)
    근데 여기서 새로고침을 하지 않으면 등록된 모습을 확인할 수 없다. 그 이유는 useEffect 함수의 두번째 인자로 빈 배열을 주면 처음 사이트에 들어갔을 1회만 렌더링이 되기 때문에 다시 값을 불러오지 않는다.
그래서 다음과 같이 수정해 주면 등록이 완료될 때 게시판이 업데이트 되는 모습을 확인할 수 있다.*/
    //불러온 데이터가 존재 할 경우
    if (robotStatusList.length > 0) {
        return (
            <div className="grid-container call-status">
                <div className="grid-item" id="item-available">
                    <h4>호출가능 로봇 수</h4>
                    <div>{countNotUsed()}</div>
                </div>
                <div className="grid-item item-state">
                    <h4>로봇 상태</h4>
                    <div>
                        <ul>
                            {robotStatusList.map(robot => (
                                <RobotStatus robot={robot} key={robot.robot_id} />
                            ))}
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
    else {  // 조회 데이터 존재하지 않을 경우
        console.log(" robotStatusList.length <= 0 존재안함")
        return (
            <div className="grid-container call-status">
                <div className="grid-item" id="item-available">
                    <h4>호출가능 로봇 수</h4>
                    <div>0</div>
                </div>
                <div className="grid-item item-state">
                    <h4>로봇 상태</h4>
                    <div>
                        <button className="data-reload" onClick={getRobotStatus}>데이터 로드</button>

                    </div>
                </div>
            </div>
        )
    }
}

export default CallStatus
