import React, { useState, useEffect, useRef } from 'react'
import axios from 'axios';
axios.defaults.baseURL = process.env.REACT_APP_SERVER_URL;    //15.165.182.10
// axios.defaults.baseURL = 'http://localhost:8080/server/src/public';    //15.165.182.10
// axios.defaults.baseURL = 'http://15.165.182.103:8080/server/src/public';     //15.165.182.103
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

function CallButton(props) {
    const userId = sessionStorage.getItem('userId');
    var responseStatus = 0;
    // const countRef = useRef(0);

    function getIsCall() {
        var call = 1;
        var count = 0;
        props.updateLoading(1); //3초동안 load할 때 버튼누르지 않도록 설정

        var interval = setInterval(() => {

            axios.get('/controller/button/is-call/' + userId)
                .then((response) => {

                    responseStatus = response.status
                    switch (responseStatus) {
                        case 201:
                            console.log("[CallButton] 현재 매장의 로봇 이동중 : " + responseStatus);
                            props.updateCalledRobot(response.data.robot_id);
                            props.updateIsCall(1);
                            call = 1;
                            // setIsCall(1);
                            break;
                        case 202:
                            console.log("[CallButton] 현재 매장의 로봇 도착 : " + responseStatus);
                            props.updateCalledRobot(response.data.robot_id);
                            props.updateIsCall(2);
                            call = 2;

                            // setIsCall(2);
                            break;
                        case 200:
                            console.log("[CallButton] 현재 매장의 로봇 호출안함 : " + responseStatus);
                            props.updateIsCall(0);
                            call = 0;

                            // setIsCall(0);
                            break;
                        default:
                            break;
                    }
                    // return 1;
                })
                .catch((error) => {
                    console.log("[CallButton] fail  get is-call button. error ->");
                    console.log(error);
                    clearInterval(interval);

                    return 0;
                })
            if (call != 1 && count >= 3) {       // 로봇이 오는중이 아니고 3번의 로드 후
                console.log("[CallButton] 로드 종료 ");
                props.updateLoading(0); //3초 로딩 후 버튼 허가
                clearInterval(interval);
            }
            count += 1;
        }, 1000);
        return;
    }



    useEffect(() => {
        getIsCall();
    }, [props.isCall])


    return (
        <div>
            <button id="call-button" onClick={props.onClick}>
                {props.isCall == 0 ? "호출" :
                    props.isCall == 2 ? "픽업대" : "오는중"}
            </button>
        </div>
    )
}

export default CallButton
